/* poppins-100 - latin */
@font-face {
    font-family: 'poppin-100';
    font-style: normal;
    font-weight: 100;
    src: url('fonts/poppin/poppins-v9-latin-100.eot'); /* IE9 Compat Modes */
    src: local('Poppins Thin'), local('Poppins-Thin'),
         url('fonts/poppin/poppins-v9-latin-100.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('fonts/poppin/poppins-v9-latin-100.woff2') format('woff2'), /* Super Modern Browsers */
         url('fonts/poppin/poppins-v9-latin-100.woff') format('woff'), /* Modern Browsers */
         url('fonts/poppin/poppins-v9-latin-100.ttf') format('truetype'), /* Safari, Android, iOS */
         url('fonts/poppin/poppins-v9-latin-100.svg#Poppins') format('svg'); /* Legacy iOS */
  }
